@import url(https://fonts.googleapis.com/css2?family=Comforter+Brush&family=M+PLUS+Code+Latin:wght@500&family=Rubik:wght@400;500&family=Ubuntu&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Yaldevi&display=swap);
*{
  margin: 0;
  padding: 0;
}
.App {
  text-align: center;
  background-color: #0b0c0f;
  
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


h1,h3{
  color: #982ac7;
  font-size: 87px;
  font-family: 'M PLUS Code Latin', sans-serif;
  padding-top: 20px;
  text-shadow: 12px 5px 10px #41045c;
}
h3{
    font-size: 20px;
    padding: 0;
    margin-right: 70%;
    margin-top: 10px;
}
.but_load{
    margin-top: 20px;
    margin-right: 70%;
}
.find{
  margin-top: 0px;
  margin-bottom: 20px;
}
.search{
  width: 30%;
  background-color: aliceblue;
  border-radius: 5px;
  color: #41045c;
}
.result{
  padding-bottom: 100px;
}
.result h3{
  margin-top: 15px;
  font-size: 25px;
}
h4{
  color: aliceblue;
  font-size: 17px;
  font-family: 'M PLUS Code Latin', sans-serif;
  text-shadow: 12px 5px 10px #41045c;
  max-width: 40%;
  margin-left: 30%;
}
.search_but{
  margin: 15px 45%;
  width: 10%;
  background-color: #3c3f3b;
}







